.documentation {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}
.documentation .doc-container {
  margin-right: auto;
  margin-left: auto;
  padding: 2rem 3rem;
  width: 100%;
}
@media (max-width: 1199.98px) {
  .documentation .doc-container {
    padding-left: 1rem;
    padding-right: 1rem;
  }
}
.documentation .doc-sidebar {
  height: auto;
  position: -webkit-sticky;
  position: sticky;
  top: 70px;
}
.documentation .sidebar-heading {
  background: #28953c;
  -webkit-box-shadow: 1px 0 0 0 #28953c;
          box-shadow: 1px 0 0 0 #28953c;
  padding-left: 1.25rem;
  height: 80px;
}
.documentation .sidebar-heading h1 {
  color: #ffffff;
  margin: 0;
  font-weight: 400;
  line-height: 80px;
}
.documentation .documentation-nav {
  padding: 1rem;
  max-height: calc(100vh - 220px);
  overflow-y: auto;
}
.documentation .documentation-nav .nav-group {
  margin-bottom: 1rem;
}
.documentation .documentation-nav .nav-group a {
  color: #2da944;
  border: none;
}
.documentation .documentation-nav .nav-group a:hover {
  color: #228134;
}
.documentation .documentation-nav .nav-group a.active {
  outline: none;
  text-decoration: none;
  font-weight: 700;
}
.documentation .documentation-nav .nav-group a:focus {
  outline: none;
  color: #2da944;
}
.documentation .documentation-nav .nav-group .nav-heading {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: end;
      -ms-flex-align: end;
          align-items: flex-end;
}
.documentation .documentation-nav .nav-group .nav-heading > i.material-icons {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.documentation .documentation-nav .nav-group .nav-heading > span {
  padding: 0;
  cursor: pointer;
  color: #2da944;
}
.documentation .documentation-nav .nav-group .nav-heading > span:hover,
.documentation .documentation-nav .nav-group .nav-heading > span:focus {
  color: #228134;
  text-decoration: underline;
}
.documentation .documentation-nav .nav-group .nav-heading > span:active,
.documentation .documentation-nav .nav-group .nav-heading > span:hover {
  outline: 0;
}
.documentation .documentation-nav .nav-group .nav-heading > span:focus {
  /* Default */
  outline: thin dotted;
  /* Webkit */
  outline: 5px auto -webkit-focus-ring-color;
  outline-offset: -2px;
}
.documentation .documentation-nav .nav-group .nav-heading i {
  cursor: pointer;
  color: rgba(60, 60, 60, 0.75);
  margin-right: 0.25rem;
  -webkit-transition: -webkit-transform 0.5s ease-in-out;
  transition: -webkit-transform 0.5s ease-in-out;
  -o-transition: transform 0.5s ease-in-out;
  transition: transform 0.5s ease-in-out;
  transition: transform 0.5s ease-in-out, -webkit-transform 0.5s ease-in-out;
}
.documentation .documentation-nav .nav-group ul {
  list-style: none;
  padding: 0 2.25rem;
  max-height: 0;
  overflow: hidden;
  -webkit-transition: max-height 0.3s;
  -o-transition: max-height 0.3s;
  transition: max-height 0.3s;
  font-size: 90%;
}
.documentation .documentation-nav .nav-group.active .nav-heading > span {
  font-weight: 700;
}
.documentation .documentation-nav .nav-group.active .nav-heading i {
  -webkit-transform: rotate(90deg);
      -ms-transform: rotate(90deg);
          transform: rotate(90deg);
  -webkit-transition: -webkit-transform 0.2s;
  transition: -webkit-transform 0.2s;
  -o-transition: transform 0.2s;
  transition: transform 0.2s;
  transition: transform 0.2s, -webkit-transform 0.2s;
}
.documentation .documentation-nav .nav-group.active ul {
  max-height: 100vh;
  -webkit-transition: max-height 1s ease-in-out;
  -o-transition: max-height 1s ease-in-out;
  transition: max-height 1s ease-in-out;
}
.documentation .doc-view {
  padding-left: 0;
  padding-right: 0;
  background: #ffffff;
  min-height: calc(100vh - 150px);
  border-left: 1px solid rgba(0, 0, 0, 0.1);
}
.documentation .view-heading {
  background: #2da944;
  height: 80px;
  border-left: 1px solid #28953c;
}
.documentation .view-heading h2 {
  color: #ffffff;
  padding-left: 2rem;
  margin: 0;
  font-weight: 400;
  line-height: 80px;
}
.documentation .doc-body {
  padding-right: 3rem;
}
@media (max-width: 991.98px) {
  .documentation .doc-body {
    padding-right: 10px;
  }
}
.documentation .doc-search {
  padding: 1rem 1rem;
  border-bottom: 1px solid rgba(0, 0, 0, 0.075);
}
.documentation .doc-search .search-results {
  position: absolute;
  left: 0;
  width: calc(100% - 2rem);
  margin: 0 1rem 0;
  padding: 0;
  list-style: none;
  border: 1px solid #d1d3d4;
  z-index: 2000;
  -webkit-box-shadow: 0 5px 50px -10px rgba(0, 0, 0, 0.75);
          box-shadow: 0 5px 50px -10px rgba(0, 0, 0, 0.75);
  display: none;
  background-color: #ffffff;
}
.documentation .doc-search .search-results .search-result a {
  color: #2da944;
  border: 0;
  display: block;
  padding: 0.25rem 0.5rem;
}
.documentation .doc-search .search-results .search-result a:hover {
  background-color: rgba(45, 169, 68, 0.1);
}
.documentation .doc-section-heading:before {
  content: "";
  display: block;
  height: 100px;
  margin: -100px 0 0;
}
.documentation .doc-toc {
  position: -webkit-sticky;
  position: sticky;
  top: 100px;
  font-size: 0.8rem;
  line-height: 1.2;
}
.documentation .doc-toc ul {
  list-style: none;
  padding-left: 0;
}
.documentation .doc-toc ul li {
  border-left: 1px solid rgba(0, 0, 0, 0.1);
  padding: 0.5rem 0 0.5rem 1rem;
}
.documentation .doc-toc ul li.active {
  border-left: 2px solid rgba(45, 169, 68, 0.7);
  font-weight: 700;
  padding-left: calc(1rem - 1px);
}
.documentation .doc-toc ul li a {
  color: #2da944;
  border: none;
}
.documentation .doc-toc ul li a:hover {
  color: #228134;
}
.documentation .showcase-panel {
  position: relative;
  background-color: #ffffff;
  padding: 1.5rem;
  margin: 0.5rem 0 1rem 0;
  border-radius: 2px;
  -webkit-box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -2px rgba(0, 0, 0, 0.2);
          box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -2px rgba(0, 0, 0, 0.2);
}
.documentation code {
  padding: 0.1rem;
  border-radius: 0.3rem;
  white-space: normal;
  background: #f5f2f0;
}
.documentation .icon-preview {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
